import React from "react";
import { NavLink } from "react-router-dom";
import web from "../images/photo1";
const Common = (props) => {
    return <>
        <section id="header">
            <div className="container-fluid ">
                <div className="row">
                    <div className="col-10 mx-auto">
                       <div className="row">
                           <div className="col-6 pt-5 mt-5">
                              <h1>{props.name} <strong className="brand-name">LetsUpgrade</strong></h1>
                              <h2 className="my-3">
                                 We are the team of talented developer making websites
                              </h2>
                              <div className="mt-4">
                                  <NavLink className="btn-get-started" to={props.visit}>{props.btname}</NavLink>
                              </div>
                           </div>
                           <div className="col-6 header-img ">
                              <img src={props.imgsrc} alt="home img" className="img-fluid animation"/>
                           </div>
                           </div>    
                    </div>
                </div>
            </div>
        </section>
    </>
}
export default Common;