import React, { useState } from "react";

const Contact = () => {
   const [data,setdata]=useState({
       fullname : "",
       phone : "",
       email : "",
       msg : "",
   });
   const inputitem =(event)=>{
        const name = event.target.name;
        const value = event.target.value;

        setdata((preval)=>{
            return{
                ...preval,[name] : value,
            }
        })
   }

    const formsubmit=(e)=>{
           e.preventDefault();
           alert(`My name is ${data.fullname}.my mobile number is ${data.phone} and email is ${data.email}.Here is what i want to say that you are ${data.msg}`)
    }
    return <>
        <div className="my-5">
            <h1 className="text-center">Contact Us</h1>
        </div>
        <div className="container">
            <div className="row">
                <div className="col-md-6 col-10 mx-auto">
                  <form onSubmit={formsubmit}>
                       <div class="mb-3">
                        <label for="exampleFormControlInput1" class="form-label">FullName</label>
                        <input type="text" class="form-control" id="exampleFormControlInput1" name="fullname" value={data.fullname} onChange={inputitem} placeholder="Enter your name" />
                    </div>
                    <div class="mb-3">
                        <label for="exampleFormControlInput1" class="form-label">Phone </label>
                        <input type="number" class="form-control" id="exampleFormControlInput1" name="phone" value={data.phone} onChange={inputitem} placeholder="mobile number" />
                    </div>
                    <div class="mb-3">
                        <label for="exampleFormControlInput1" class="form-label">Email address</label>
                        <input type="email" class="form-control" id="exampleFormControlInput1" name="email" value={data.email} onChange={inputitem} placeholder="name@example.com" />
                    </div>
                    <div class="mb-3">
                        <label for="exampleFormControlTextarea1" class="form-label">Message</label>
                        <textarea class="form-control" name="msg" value={data.msg} onChange={inputitem} id="exampleFormControlTextarea1" rows="3"></textarea>
                    </div>
                    <div class="col-12">
                        <button class="btn btn-outline-primary" type="submit">Submit form</button>
                    </div>
                    </form>

                </div>
            </div>
        </div>
    </>
}
export default Contact;